<template>
  <div class="container">
    <h3>Editar Empleado</h3>
    <v-form @submit.prevent="updateEmployee" ref="form" lazy-validation>
      <v-text-field disable
        v-model="id"
        data-vv-name="id"
        label="ID"
        :error-messages="errors.collect('id')"
        v-validate="'required|min:1'"
        required
      ></v-text-field>
      <v-text-field
        v-model="name"
        data-vv-name="name"
        :error-messages="errors.collect('name')"
        v-validate="'required|min:2'"
        label="Nombre"
        required
      ></v-text-field>
      <v-text-field
        v-model="area"
        data-vv-name="area"
        :error-messages="errors.collect('area')"
        v-validate="'required|min:2'"
        label="Área"
        required
      ></v-text-field>
      <v-text-field
        v-model="email"
        data-vv-name="email"
        label="E-mail"
        :error-messages="errors.collect('email')"
        v-validate="'required|min:2'"
        required
      ></v-text-field>
      <v-text-field
        v-model="city"
        data-vv-name="city"
        :error-messages="errors.collect('city')"
        v-validate="'required|min:2'"
        label="Ciudad"
        required
      ></v-text-field>
      <v-btn @click="backToHome" color="error" class="mr-4">Regresar</v-btn>
      <v-btn type="submit" color="warning">Guardar</v-btn>
    </v-form>
  </div>
</template>
<script>
import db from "./firebaseInit";
import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate);

export default {
  $_veeValidate: {
    validator: "new"
  },
  name: "edit-employee",
  data() {
    return {
      id: null,
      name: null,
      area: null,
      email: null,
      city: null,
      dictionary: {
        attributes: {
          email: "E-mail Address"
          // custom attributes
        },
        custom: {
          id: {
            required: () => "ID es requerido",
            min: "Más de 1 caracter"
          },
          name: {
            required: () => "Nombre es requerido",
            min: "Más de dos caracteres"
          },
          area: {
            required: () => "Área es requerido",
            min: "Más de dos caracteres"
          },
          email: {
            required: () => "E-mail es requerido",
            min: "Más de dos caracteres"
          },
          city: {
            required: () => "Ciudad es requerido",
            min: "Más de dos caracteres"
          }
        }
      }
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("employees")
      .where("id", "==", to.params.employee_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(row => {
          next(vm => {
            vm.id = row.data().id;
            vm.name = row.data().name;
            vm.area = row.data().area;
            vm.city = row.data().city;
            vm.email = row.data().email;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    backToHome() {
      this.$router.push("/");
    },

    fetchData() {
      db.collection("employees")
        .where("id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(row => {
            this.id = row.data().id;
            this.name = row.data().name;
            this.area = row.data().area;
            this.city = row.data().city;
            this.email = row.data().email;
          });
        });
    },

    updateEmployee() {
      db.collection("employees")
        .where("id", "==", this.$route.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(row => {
            row.ref
              .update({
                id: this.id,
                name: this.name,
                area: this.area,
                city: this.city,
                email: this.email
              })
              .then(() => {
                this.$router.push({
                  name: "view-employee",
                  params: { employee_id: this.id }
                });
              });
          });
        });
    }
  }
};
</script>